import {
	sendAjax
} from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const {getCleanerWork,setCleanerWork} = config.api;
export default {
	data() {
		return {
			modalName:null,
			id: null,
			basicsList: [{
				name: '预约时间'
			}, {
				name: '完善信息'
			}, {
				name: '估价'
			}],
			// 0 预约时间 1 预约时间+完善信息 2  预约时间+完善信息+提交
			scroll: 0,
			detail: '',
			chooseType: '',
			chooseData: '',
			dataList: [],
			yuyueList: {}
		};
	},
	methods: {
		onLoad: function(e) {
			this.id = uni.getStorageSync('userDetail').cleaner.id;
			
			this.getWorkTime();
			this.getCleanerWork();
		},
		paiban(e){
			console.log('排班')
			this.modalName = e.currentTarget.dataset.target;
			this.chooseType = e.currentTarget.dataset.type;
			this.chooseData = e.currentTarget.dataset.data;
			console.log(this.chooseType)
			console.log(this.chooseData)
			
		},
		getCleanerWork(){
			var that = this;
			let infoOpt = {
				url: getCleanerWork,
				type: 'POST',
				data: {
					cleanerId:this.id
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				var workList = res;
				for(var i in workList){
					workList[i].workDate = workList[i].workDate.substring(5,10)
				}
				var yuyueList = {}
				for(var i in workList){
					yuyueList[workList[i].workDate] = workList[i].statusMor.toString() + workList[i].statusAft.toString()
				}
				console.log(yuyueList)
				that.yuyueList = yuyueList
				console.log(yuyueList['12-12'])
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		cancel(){
			var type = this.chooseType;
			var work_date = this.chooseData;
			if(type == 'mor'){
				var status_mor = 0;
			}else{
				var status_aft = 0;
			}
			var data = {
				id:this.id,
				work_date:work_date,
				type:type
			}
		},
		confirm(){
			var type = this.chooseType;
			var work_date = this.chooseData;
			// if(type == 'mor'){
			// 	status_mor = 1;
			// }else{
			// 	status_aft = 1;
			// }
			var that = this;
			let infoOpt = {
				url: setCleanerWork,
				type: 'POST',
				data: {
					cid:this.id,
					workDate:work_date,
					type:type
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		hideModal(e) {
			this.modalName = null
		},
		getWorkTime() {
			var arr = []
			for (let i = 0; i < 7; i++) {
				arr.push(this.dealTime(i))
			}
			console.log(arr)
			this.dataList = arr;
		},
		// 处理未来七天的函数
		dealTime: function(num) { // num：未来天数
			var time = new Date() // 获取当前时间日期
			var date = new Date(time.setDate(time.getDate() + num + 1)).getDate() //这里先获取日期，在按需求设置日期，最后获取需要的
			if (date < 10) date = '0' + date;
			var year = time.getFullYear();
			var month = time.getMonth() + 1 // 获取月份
			var day = time.getDay() //  获取星期
			switch (day) { //  格式化
				case 0:
					day = "周日"
					break
				case 1:
					day = "周一"
					break
				case 2:
					day = "周二"
					break
				case 3:
					day = "周三"
					break
				case 4:
					day = "周四"
					break
				case 5:
					day = "周五"
					break
				case 6:
					day = "周六"
					break
			}
			var obj = {
				workDate:year + '-' + month + '-' + date,
				workTime: day,
				statusMor: 0,
				statusAft: 0
			}
			return obj // 返回对象
		},
		
		showModal(e) {
			
		}
	}
};
